@charset "UTF-8";
html {
  font-size: 10px;
}

body {
  background: #262161;
  background: #efefef;
}
body:before {
  content: "";
  display: block;
  background: linear-gradient(90deg, #48ded4 0%, #a026bf 20%, #e82c75 60%, #FFC40E 85%, #48ded4 95%);
  height: 10px;
  width: 100%;
}

.inner {
  max-width: 900px;
  margin: 0 auto;
}
@media all and (max-width: 900px) {
  .inner {
    padding: 2rem;
  }
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

img {
  max-width: 100%;
}

