@font-face {
  font-family: "panama";
  src: url("../../fonts/panama-bold-webfont.woff2") format("woff2"), url("../../fonts/panama-bold-webfont.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}
html {
  font-size: 10px;
}

body {
  font-size: 1.5rem;
  color: #303030;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: 5rem;
  font-family: "Panama";
  font-weight: 600;
  letter-spacing: -1px;
}

a {
  text-decoration: none;
  color: #303030;
}

p a {
  border-bottom: 2px solid #FFC40E;
}

p {
  line-height: 2;
  font-size: 1.6rem;
}

.title {
  word-wrap: break-word;
  position: relative;
  z-index: 2;
  margin: 0;
  font-size: 40px;
  z-index: 2;
  line-height: 1.1;
  transform: skew(0, -3deg);
  line-height: 1.1;
}
.title--long {
  font-size: 30px;
}
.title:before {
  content: "";
  width: 50px;
  left: 0;
  top: 0;
  height: 100%;
  display: block;
  position: absolute;
  z-index: -1;
  transform: skew(-5deg);
}
.title a {
  border-bottom: 0;
  background-image: linear-gradient(to right, rgba(255, 196, 14, 0.8) 100%, rgba(255, 196, 14, 0.8) 50%);
}

.title--single {
  max-width: 600px;
  margin-top: -9rem;
  font-size: 10rem;
  text-align: center;
}

input, textarea {
  width: 100%;
  padding: 10px;
  border: 1px solid #e6e6e6;
}
input.input--error, textarea.input--error {
  border-color: red;
}

label {
  padding: 10px 0;
  display: block;
}

.form {
  background: white;
  padding: 2rem;
  box-shadow: linear-gradient(90deg, #48ded4 0%, #a026bf 20%, #e82c75 60%, #FFC40E 85%, #48ded4 95%);
}
.form + .form {
  margin-top: 4rem;
}
.form h2 {
  margin: 0;
  font-size: 2rem;
  padding-bottom: 2rem;
  margin-bottom: 2rem;
  border-bottom: 1px solid #e6e6e6;
}

.search {
  position: relative;
  width: 100%;
  display: flex;
}
.search__results {
  background: white;
  position: absolute;
  width: 100%;
  top: 100%;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
  z-index: 5;
  left: 0;
}
.search__input {
  font-size: 30px;
  background: none;
  color: white;
  outline: 0;
  border: 0;
}
.search__result {
  padding: 10px;
  display: block;
  border-bottom: 1px solid #ececec;
}
.search__result--active {
  background: #f1f1f1;
}

@keyframes slideIn {
  0% {
    transform: translateX(-10px);
  }
  50% {
    transform: translateX(10px);
  }
  100% {
    transform: translateX(0px);
  }
}
.flash {
  background: white;
  box-shadow: 0 3px 10px rgba(0, 0, 0, 0.15);
  margin-bottom: 2rem;
  padding: 2rem;
  position: relative;
  z-index: 1;
  border-radius: 3px;
  display: flex;
  animation: slideIn forwards 0.1s ease-in-out 2;
  animation-timing-function: cubic-bezier(0.01, 1.68, 0.58, 1);
}
.flash + .flash {
  animation-delay: 0.55s;
}
.flash + .flash + .flash {
  animation-delay: 0.6s;
}
.flash__text {
  flex: 1;
}
.flash__remove {
  background: none;
  border: 0;
}
.flash__remove:hover {
  color: #FFC40E;
}
.flash:after {
  content: "";
  display: block;
  background: white;
  position: absolute;
  width: calc(100% - 16px);
  height: calc(100% - 16px);
  top: 8px;
  left: 8px;
  z-index: -1;
  box-shadow: 0 0 2px 2px rgba(0, 0, 0, 0.1);
  border-radius: 3px;
}
.flash--success {
  background: linear-gradient(135deg, #d2ff52 0%, #91e842 100%);
}
.flash--error {
  background: linear-gradient(20deg, red 0%, #c80000 100%);
}
.flash--info {
  background: linear-gradient(35deg, #f1e767 0%, #feb645 100%);
}
.flash p {
  margin: 0;
}

.nav {
  display: flex;
  list-style: none;
  margin: 0;
  padding: 0;
  justify-content: space-between;
  background: #303030;
}
.nav__section {
  display: flex;
}
.nav__section--search {
  flex: 1 1 auto;
}
.nav__item {
  display: flex;
}
.nav__link {
  background: #303030;
  color: white;
  border-right: 1px solid rgba(255, 255, 255, 0.1);
  text-transform: uppercase;
  padding: 1.2rem 2rem 1rem 2rem;
  display: block;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  transition: transform 0.2s;
  border-bottom: 5px solid transparent;
}
.nav__link svg {
  width: 40px;
  transition: all 0.2s;
  fill: white;
  margin-bottom: 1rem;
}
.nav__link:hover, .nav__link--active {
  border-bottom-color: rgba(0, 0, 0, 0.2);
  border-right-color: rgba(0, 0, 0, 0.05);
  background: linear-gradient(90deg, #48ded4 0%, #a026bf 20%, #e82c75 60%, #FFC40E 85%, #48ded4 95%) fixed;
}
.nav__link:hover svg, .nav__link--active svg {
  transform: scale(1.2);
}
.nav__link--logo:hover svg {
  transform: none;
}
.nav__link--logo svg {
  width: 200px;
  margin: 0;
}

@media all and (max-width: 1200px) {
  .nav__link {
    padding-left: 1rem;
    padding-right: 1rem;
  }
  .nav__link svg {
    width: 30px;
  }
  .nav__link--logo svg {
    width: 100px;
  }
}
@media all and (max-width: 900px) {
  .nav {
    flex-wrap: wrap;
  }
  .nav__section {
    order: 1;
  }
  .nav__section--search {
    border-top: 1px solid rgba(255, 255, 255, 0.1);
    order: 2;
    width: 100%;
  }
}
@media all and (max-width: 650px) {
  .nav__section {
    width: 100%;
  }
  .nav__section--user {
    order: -1;
    border-bottom: 1px solid rgba(255, 255, 255, 0.1);
  }
  .nav__section--user .avatar {
    min-width: 20px;
    height: 20px;
  }
  .nav__section--user svg {
    display: none;
  }
  .nav__item {
    width: 33.333333%;
  }
  .nav__link {
    width: 100%;
  }
  .nav__link svg {
    max-width: 100%;
  }
  .nav__link--logo svg {
    width: 100%;
  }

  .heart-count:before {
    content: "♥️";
    margin-right: 1rem;
  }
}
.table {
  width: 100%;
}
.table td {
  padding: 5px;
}
.table tr:nth-child(even) {
  background: white;
}
.table tr:nth-child(odd):not(:first-child) {
  background: #f7f7f7;
}
.table thead tr {
  background: #303030;
  color: white;
}

.stores {
  display: flex;
  flex-wrap: wrap;
  margin: -1.25em 0 0 -1.25em;
}

.store {
  background: white;
  margin: 1.25em 0 0 1.25em;
  width: calc(33.3333% - 1.25em);
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1), 0 5px 10px rgba(0, 0, 0, 0.05);
}
.store--wide {
  width: 100%;
}
.store__details {
  padding: 2rem;
}
.store__details p {
  line-height: 1.2;
  margin-bottom: 0;
}
@media all and (max-width: 850px) {
  .store {
    width: calc(50% - 1.25em);
  }
}
@media all and (max-width: 550px) {
  .store {
    width: 100%;
  }
}

.store__actions {
  position: relative;
  z-index: 2;
  border-bottom: 1px solid rgba(0, 0, 0, 0.2);
  box-shadow: 0 1px 0 rgba(0, 0, 0, 0.2), 0 1px 0 rgba(255, 255, 255, 0.2);
  margin-bottom: 50px;
  padding-top: 10px;
  padding-bottom: 20px;
  display: flex;
  color: white;
  align-items: center;
  justify-content: space-around;
}

.store__action {
  font-size: 10px;
}
.store__action svg {
  width: 25px;
  fill: white;
}
.store__action--edit a {
  border-bottom: 0;
}

.store__hero {
  position: relative;
  padding: 0 10px 0 10px;
  text-align: right;
}
.store__hero:before {
  display: block;
  content: "";
  position: absolute;
  height: 100%;
  width: 100%;
  top: 0;
  right: 0;
  background: linear-gradient(45deg, #05b3d6 0%, rgba(5, 179, 214, 0) 70%), linear-gradient(135deg, #0958ec 10%, rgba(9, 88, 236, 0) 80%), linear-gradient(225deg, #d209ec 10%, rgba(210, 9, 236, 0) 80%), linear-gradient(315deg, #05e129 100%, rgba(5, 225, 41, 0) 70%);
  opacity: 0.6;
  z-index: 1;
  clip-path: polygon(0 0, 100% 0, 100% 90%, 0% 100%);
}
.store__hero img {
  height: 100%;
  width: 100%;
  top: 0;
  right: 0;
  position: absolute;
  left: 0;
  object-fit: cover;
  clip-path: polygon(0 0, 100% 0, 100% 90%, 0% 100%);
}

.tags {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  margin-bottom: 2rem;
  flex-wrap: wrap;
}

.tag {
  display: block;
  margin-right: 1rem;
}
.tag__choice {
  display: flex;
  justify-content: center;
  align-items: center;
}
.tag__choice input {
  width: auto;
  right: -1.5rem;
  margin-right: -1rem;
  position: relative;
  z-index: 2;
}
.tag__choice input + label {
  background: #e6e6e6;
  padding: 1rem 1rem 1rem 4rem;
}
.tag__choice input:checked + label {
  background: #FFC40E;
}
.tag__link {
  background: #FFC40E;
  color: #303030;
  display: flex;
  padding-left: 10px;
  clip-path: polygon(10px 0%, 100% 1%, 100% 100%, 10px 100%, 0% 50%);
}
.tag__link--active {
  background: #827cd2;
}
.tag__link--active .tag__text {
  color: white;
}
.tag__text {
  padding: 1rem 1rem;
  display: block;
}
.tag__count {
  padding: 1rem 1rem;
  background: white;
  border-left: 3px solid rgba(0, 0, 0, 0.1);
  margin-right: -1px;
  /* ugh */
  background-clip: padding-box;
}

.single__hero {
  height: 500px;
  position: relative;
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
  clip-path: polygon(0 0, 100% 0, 100% calc(100% - 2vw), 0% 100%);
}
.single__hero:before {
  display: block;
  content: "";
  position: absolute;
  height: 100%;
  width: 100%;
  top: 0;
  right: 0;
  background: linear-gradient(45deg, #05b3d6 0%, rgba(5, 179, 214, 0) 70%), linear-gradient(135deg, #0958ec 10%, rgba(9, 88, 236, 0) 80%), linear-gradient(225deg, #d209ec 10%, rgba(210, 9, 236, 0) 80%), linear-gradient(315deg, #05e129 100%, rgba(5, 225, 41, 0) 70%);
  opacity: 0.6;
  z-index: 1;
  clip-path: polygon(0 0, 100% 0, 100% calc(100% - 2vw), 0% 100%);
}
.single__image {
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  object-fit: cover;
  position: absolute;
}
.single__details {
  background: white;
  position: relative;
  padding: 3rem;
  margin-top: -10rem;
  box-shadow: 0 0 10px 2px rgba(0, 0, 0, 0.15);
}
.single__map {
  width: calc(100% + 6rem);
  max-width: none;
  margin-top: -3rem;
  margin-left: -3rem;
}
.single__location {
  margin: 0;
  margin-top: -3rem;
  margin-right: -5rem;
  background: #303030;
  color: white;
  display: inline-block;
  float: right;
  position: relative;
  padding: 1rem;
}
@media all and (max-width: 850px) {
  .single__location {
    margin-right: 0;
  }
}

.heart__button {
  background: none;
  border: 0;
  outline: 0;
  position: relative;
}
.heart__button:after {
  content: "♥️";
  font-size: 20px;
  position: absolute;
  opacity: 0;
  top: 0;
}
.heart__button svg {
  width: 25px;
  fill: white;
}
.heart__button--hearted svg {
  fill: red;
}
.heart__button--float:after {
  animation: fly 2.5s 1 ease-out;
}

@keyframes fly {
  0% {
    transform: translateY(0);
    left: 0;
    opacity: 1;
  }
  20% {
    left: 20px;
  }
  40% {
    left: -20px;
  }
  60% {
    left: 20px;
  }
  80% {
    left: -20px;
  }
  100% {
    transform: translateY(-400px);
    opacity: 0;
    left: 20px;
  }
}
.review {
  background: white;
  border: 1px solid #e6e6e6;
  border-bottom: 0;
  border-bottom: 1px solid #e6e6e6;
  margin-bottom: 2rem;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  position: relative;
  padding-left: 5px;
}
.review:before {
  width: 5px;
  left: 0;
  content: "";
  display: block;
  background: linear-gradient(0, #48ded4 0%, #a026bf 20%, #e82c75 60%, #FFC40E 85%, #48ded4 95%);
  position: absolute;
  height: 100%;
  background-attachment: fixed;
}
.review__header {
  border-bottom: 1px solid #e6e6e6;
  display: flex;
}
.review__header > * {
  border-right: 1px solid #e6e6e6;
  padding: 0.2rem;
  display: flex;
  align-items: center;
  flex: 1;
  justify-content: center;
}
.review__header > *:last-child {
  border-right: 0;
}
.review__author {
  justify-content: flex-start;
  padding-left: 2rem;
  display: flex;
}
.review__author .avatar {
  margin-right: 2rem;
}
.review__time {
  font-size: 1.2rem;
  color: #999999;
}
.review__stars {
  color: #FFC40E;
}
.review__body {
  padding: 2rem;
}
.review p {
  white-space: pre-wrap;
}

/*
  Reviewer Form
 */
.reviewer {
  position: relative;
  box-shadow: 0 0px 10px rgba(0, 0, 0, 0.2);
  margin-bottom: 2rem;
}
.reviewer__stars {
  display: flex;
  justify-content: center;
}
.reviewer__stars input {
  display: none;
}
.reviewer__stars input:checked ~ label {
  color: #FFC40E;
}
.reviewer__stars input + label {
  font-size: 0;
  /* These are in the opposite DOM order
     re-order them to be visually in normal order
     This is fine for accessibility because our labels have for()
   */
}
.reviewer__stars input + label:before {
  content: "★";
  font-size: 2rem;
}
.reviewer__stars input + label[for=star5] {
  order: 5;
}
.reviewer__stars input + label[for=star4] {
  order: 4;
}
.reviewer__stars input + label[for=star3] {
  order: 3;
}
.reviewer__stars input + label[for=star2] {
  order: 2;
}
.reviewer__stars input + label[for=star1] {
  order: 1;
}
.reviewer__stars input + label:hover, .reviewer__stars input + label:hover ~ label {
  color: #ffdd74;
}
.reviewer textarea {
  border: 0;
  outline: 0;
  font-size: 2rem;
  padding: 2rem;
  height: 200px;
}
.reviewer__meta {
  display: flex;
  justify-content: center;
  align-items: center;
  border-top: 1px solid #e6e6e6;
}
.reviewer__meta > * {
  flex: 1;
}

.pagination {
  display: flex;
  align-items: center;
  background: white;
  margin-top: 2rem;
  margin-bottom: 2rem;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1), 0 5px 10px rgba(0, 0, 0, 0.05);
}
.pagination__next {
  flex: 0 0 auto;
  padding: 1rem 3rem 1rem 1rem;
}
.pagination__next a:after {
  margin-left: 1rem;
  content: "→";
}
.pagination__prev {
  flex: 0 0 auto;
  padding: 1rem 1rem 1rem 3rem;
}
.pagination__prev a:before {
  margin-right: 1rem;
  content: "←";
}
.pagination__text {
  flex: 3 0 auto;
  padding: 1rem;
  text-align: center;
}

.map {
  padding: 20px;
  background: white;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1), 0 5px 10px rgba(0, 0, 0, 0.05);
}

#map {
  height: 500px;
}

.popup {
  width: 300px;
}
.popup img {
  width: 100%;
}

.hide {
  text-indent: -999999px;
  font-size: 0;
}

.button {
  border: 0;
  background: #FFC40E;
  color: #303030;
  font-family: "Panama";
  font-weight: 600;
}

.avatar {
  min-width: 50px;
  height: 50px;
  border-radius: 50%;
  align-self: center;
}

.card {
  background: white;
  padding: 5rem;
  box-shadow: 0 1px 10px rgba(0, 0, 0, 0.15);
}

pre.error {
  padding: 5rem;
  background: white;
  line-height: 3;
  white-space: pre-line;
}